body {
  margin: 0;
  background-color: rgb(19, 18, 18);
  font-family: IBM Plex Sans, "Helvetica Neue", "sans-serif";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-flow: column;
}

.home-left-panel {
  background-color: rgba(255, 255, 255, 0.95);
  width: 100%;
  height: 100px;
  flex: 0 0 auto;
  position: fixed;
}

.home-right-panel {
  width: 100%;
  flex: 1 1 auto;
}

.app-footer {
  color: rgb(133, 131, 131);
  text-align: center;
  font-style: italic;
  font-size: 11px;
  font-family: IBM Plex Mono;
  margin-bottom: 4px;
}
.gallery-item {
  margin: 10px 0px;
  border: 1px solid gray;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-position: center;
  cursor: pointer;
}
.gallery-item .gallery-item-center-text-container {
  background-color: rgb(233, 233, 233);
  border: 1px solid black;
  width: 50%;
  height: auto;
  display: flex;
  flex-flow: column;
}
.gallery-item .gallery-item-center-text-container .gallery-item-project-category-container {
  text-align: center;
  padding: 3px 0px;
  font-family: IBM Plex Mono;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 10pt;
}
.gallery-item .gallery-item-center-text-container .gallery-item-title-and-subtitle-container .gallery-item-title-container {
  text-align: center;
  font-family: IBM Plex Sans;
  font-size: 22pt;
  font-weight: 600;
  margin: 10px 0px;
}
.gallery-item .gallery-item-center-text-container .gallery-item-title-and-subtitle-container .gallery-item-subtitle-container {
  text-align: center;
  margin: 0px 10px;
}

@media only screen and (max-width: 875px) {
  .gallery-item {
    height: 600px !important;
    margin-left: 10px;
  }
  .gallery-item .gallery-item-center-text-container {
    width: 70% !important;
    height: unset !important;
  }
  .gallery-item .gallery-item-title-and-subtitle-container {
    padding: 10px !important;
  }
}
