.CV-HTML {
  color: white;
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  max-width: 1100px;
}
.CV-HTML .cv-left {
  margin-bottom: 50px;
  padding-right: 30px;
}
.CV-HTML .cv-left .cv-header {
  color: rgb(103, 204, 194);
}
.CV-HTML .cv-left .cv-content-block {
  padding-left: 20px;
}
.CV-HTML .cv-left .cv-experience-location-position {
  display: block;
  font-weight: 600;
  margin-top: 20px;
}
.CV-HTML .cv-left .cv-experience-timeframe {
  display: block;
  font-style: italic;
}
.CV-HTML .cv-left .cv-experience-description {
  display: block;
  margin-top: 5px;
}
.CV-HTML .cv-left .cv-list {
  list-style: none;
  padding-left: 20px;
}
.CV-HTML .cv-right {
  margin-top: 30px;
}
.CV-HTML .cv-right .cv-contact-info {
  border: 2px solid rgb(103, 204, 194);
  padding: 10px;
}
.CV-HTML .cv-right .cv-contact-info .cv-info-line {
  display: block;
}
.CV-HTML .cv-download-button-container {
  border: 1px solid red;
}
.CV-HTML .cv-download-button-container .cv-download-button {
  border: 2px solid rgb(103, 204, 194);
}

@media only screen and (max-width: 875px) {
  .CV-HTML {
    flex-direction: column-reverse;
  }
  .CV-HTML .cv-right .cv-contact-info {
    margin: 30px;
  }
  .CV-HTML .cv-left {
    padding: 10px;
  }
}
